@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap);
body {
    background: linear-gradient(rgba(30, 27, 38, 0.95), rgba(30, 27, 38, 0.95)), url(https://i.ibb.co/FDGqCmM/papers-co-ag74-interstellar-wide-space-film-movie-art-33-iphone6-wallpaper.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: repeat;
}


h1, h2, h3, h4, h5 {
    font-family: "Montserrat", sans-serif;
    color: #e7e7e7;
    margin: 0px;
}

h1 {
    font-size: 1.3rem;
    font-weight: 400;
}

h5 {
    font-size: 0.6rem;
    font-weight: 400;
    text-transform: uppercase;
}
.card{
    background-color: #1e1b26;
    box-shadow: 5px 5px 55px -14px black;
}

/* .card :hover{
    border: 2px solid #fe4141;
} */

.card-body {
    padding: 0 1rem 1rem 1rem;
}

.imgMovie:hover{
    border: 3px solid #fe4141;
}


.button_red {
    display: block;
    border: 1px solid #fe4141;
    border-radius: 5px;
    padding: 4px;
    width: 90%;
    background-color: #1e1b26;
    color: #fe4141;
    font-size: 0.7rem;
    margin-top: 25px;
}
  
.button_red:hover {
    background-color: #fe4141;
    color: white;
}

.imgMovie{
    -webkit-mask-image: -webkit-gradient(linear, left top, left bottom, color-stop(0, black), color-stop(0.35, black), color-stop(0.5, black), color-stop(0.65, black), color-stop(0.85, rgba(0, 0, 0, 0.6)), color-stop(1, transparent));
}

.selectedSinopsis{
    font-size: 1rem;
    color: white;
    margin-top: 20px;
}

.navbar_movies{
    display: flex;
    list-style-type: none;
}

.navbar_movies .button_red{
    width: 100px;
    margin-right: 10px;
    text-transform: uppercase;
    font-weight: 700;
}

.notMovie{
    width: 100%;
    height: 100px;
    background-color: dimgray;
}

.button_red_top {
    display: block;
    border: 1px solid #fe4141;
    border-radius: 5px;
    padding: 4px;
    background-color: #1e1b26;
    color: #fe4141;
    font-size: 0.7rem;
    margin-top: 25px;
    width: 90px;
    text-transform: uppercase;
    font-weight: bold;
}

.button_red_top:hover {
    background-color: #fe4141;
    color: white;
}
